import React, { Component } from 'react';
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption
} from 'reactstrap';
import './styles.css';

import pic1 from '../../img/1.jpg';
import pic2 from '../../img/2.jpg';
import pic3 from '../../img/3.jpg';
import pic4 from '../../img/weebly1.png';
import pic5 from '../../img/weebly2.png';
import pic6 from '../../img/weebly3.png';


const items = [
  {
    src: pic1,
    altText: 'After completing a career as a professional ballet dancer with San Francisco Ballet, Aaron Orza has grown into the role of freelance Ballet Master. He received mentorship from the best the ballet world has to offer and has had the privilege of performing with world-renowned companies and artists. His first big break came when he was just fourteen',
    caption: 'Example Slide 1',
		className: 'specialClass'
  },
  {
    src: pic2,
    altText: 'Slide 2',
    caption: 'Example Slide 2'
  },
  {
    src: pic3,
    altText: 'Slide 3',
    caption: 'Slide 3'
  },
	{
    src: pic4,
    altText: 'Slide 4',
    caption: 'Slide 4'
  },
  {
    src: pic5,
    altText: 'Slide 5',
    caption: 'Slide 5'
  },
  {
    src: pic6,
    altText: 'Slide 6',
    caption: 'Slide 6'
  }
];

class CarouselFrame extends Component {
	constructor(props) {
    super(props);
    this.state = { activeIndex: 0 };
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
    this.goToIndex = this.goToIndex.bind(this);
    this.onExiting = this.onExiting.bind(this);
    this.onExited = this.onExited.bind(this);
  }

  onExiting() {
    this.animating = true;
  }

  onExited() {
    this.animating = false;
  }

  next() {
    if (this.animating) return;
    const nextIndex = this.state.activeIndex === items.length - 1 ? 0 : this.state.activeIndex + 1;
    this.setState({ activeIndex: nextIndex });
  }

  previous() {
    if (this.animating) return;
    const nextIndex = this.state.activeIndex === 0 ? items.length - 1 : this.state.activeIndex - 1;
    this.setState({ activeIndex: nextIndex });
  }

  goToIndex(newIndex) {
    if (this.animating) return;
    this.setState({ activeIndex: newIndex });
  }

	render() {
		const { activeIndex } = this.state;

    const slides = items.map((item) => {
      return (
        <CarouselItem
          onExiting={this.onExiting}
          onExited={this.onExited}
          key={item.src}
        >
          <img src={item.src} alt={item.altText} id="carouselImage"/>
          <CarouselCaption captionText={item.altText} captionHeader={item.caption} className={item.className}/>
        </CarouselItem>
      );
    });

    return (
      <div>
					<Carousel
	        activeIndex={activeIndex}
	        next={this.next}
	        previous={this.previous}
					className="carouselBackground"
	      >
	        <CarouselIndicators items={items} activeIndex={activeIndex} onClickHandler={this.goToIndex} />
	        {slides}
	        <CarouselControl direction="prev" directionText="Previous" onClickHandler={this.previous} />
	        <CarouselControl direction="next" directionText="Next" onClickHandler={this.next} />
	      </Carousel>
			</div>
    );
	}
}

export default CarouselFrame;



